.social-list {
  list-style-type: none;
  margin: 0 -14px 0 auto;
  padding: 0;
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  min-width: 141px;
  max-width: 235px;
}

.social-list__link {
  display: block;
  max-height: 47px;
  max-width: 47px;
  padding: 13.5px;
}

@include vp-1023 {
  .social-list {
    margin: 0 -10px 0 auto;
    min-width: 117px;
    max-width: 156px;
  }

  .social-list__link {
    max-height: 39px;
    max-width: 39px;
    padding: 11px;

    svg {
      width: 16.42;
      height: 16px;
    }
  }
}

@include vp-767 {
  .social-list {
    justify-content: left;
    margin: 0 auto 0 -15px;
    min-width: 100px;
    max-width: 180px;
  }

  .social-list__link {
    max-height: 35px;
    max-width: 35px;
    padding: 7px;
    margin-right: 5px;
  }
}
